* {
	box-sizing: border-box;
	color: #111;
}
#verbs {
	padding: 70px;
}
.selection-container {
	display: flex;
	flex-flow: column nowrap;
	align-items: flex-start;
	gap: 5px;
}
.button {
	margin-top: 20px;
	border: 2px solid #111;
	padding: 5px 10px;
	font-weight: 500;
	display: flex;
}
.button:hover {
	background-color: #111;
	color: #fff;
}
.test-container {
	width: 100%;
	/* padding: 70px; */
	display: flex;
	flex-flow: column nowrap;
	gap: 40px;
}
.selection-input {
	font-size: 20px;
	/* padding: 0 0 5px; */
}

#question-box {
	/* min-width: 600px; */
	width: 100%;
	/* padding: 70px; */
	display: flex;
	flex-flow: row nowrap;
	gap: 30px;
}

.question-container {
	display: flex;
	align-content: flex-start;
	/* flex-flow: column nowrap; */
	flex-flow: row wrap;
	flex: 5;
}

.hint-container {
	display: flex;
	flex-flow: column nowrap;
	flex: 2;
}

.hint {
	padding: 10px 0;
}

.hint-title {
	/* text-align: center; */
	font-size: 22px;
	font-style: italic;
}
.hint-title:hover {
	text-decoration: underline;
}
.hint-contents {
	font-size: 18px;
}

.question-text {
	font-size: 28px;
	flex-basis: 100%;
}

.answer-container {
	display: flex;
	flex-flow: row;
	flex-basis: 85%;
	gap: 15px;
}
/* .answer-container > div:first-child {
   padding: 10px 10px 10px 0;
} */
.pronoun {
	font-size: 36px;
	display: flex;
	flex-basis: 15%;
	justify-content: center;
	align-items: center;
}
.answer-input {
	font-size: 36px;
	background: #e2e2e2;
	border: none;
	padding: 10px;
	flex-basis: 85%;
}
.answer-input:focus-visible {
	outline: none;
}
.icon {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-basis: 15%;
}
.icon > span:first-child {
	font-size: 30px;
	/* padding: 10px 20px; */
}

.result {
	flex-basis: 100%;
}
.footer-container {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
}
.count-box {
	text-align: right;
	font-size: 18px;
}
.red span {
	color: red;
}
.green span {
	color: green;
}

.hidden {
	display: none;
}
#results-box {
	display: flex;
	flex-flow: column nowrap;
}
.results-container {
	display: flex;
	flex-flow: row wrap;
	gap: 40px;
}
.results {
	display: flex;
	flex: 1;
	flex-flow: column nowrap;
}
#results-box .footer-container {
	justify-content: flex-end;
}